/**
 * Provides functions to parse question content from the Moodle quiz page, extracting relevant information
 * like question text, code, examples, and expected output, depending on the question type.
 *
 * @module     block_sage/parser
 * @copyright  2025 Vasilevskiy Vladimir <vivasilevskiy_1@edu.hse.ru>
 * @license    http://www.gnu.org/copyleft/gpl.html GNU GPL v3 or later
 */
define(['jquery'], function($) {
    /**
     * Creates a parser for 'description' type questions.
     * @returns {{getType: (function(): string), parseQuestionToJson: (function(*): {type, text: *})}}
     */
    function getDescriptionParser() {
        return {
            getType: function() {
                return "description";
            },
            parseQuestionToJson: function(question) {
                const questionText = $(question).find('.qtext').text().trim();
                return {
                    type: this.getType(),
                    text: questionText
                };
            }
        };
    }

    /**
     * Creates a parser for 'coderunner' type questions.
     *
     * @returns {{getType: (function(): string), parseQuestionToJson: (function(*): {text: *, examples: *, code: *, output: *})}}
     */
    function getCoderunnerParser() {
        return {
            getType: function () {
                return "coderunner";
            },
            parseQuestionToJson: function (question) {
                let questionText = '';
                $(question).find('.formulation.clearfix').contents().each(function() {
                    if ($(this).hasClass('for-example-para') || $(this).hasClass('prompt')) {
                        return false;
                    }
                    if (this.nodeType === 3) {
                        questionText += this.textContent.trim() + '\n';
                    } else if (this.nodeType === 1) {
                        questionText += $(this).text().trim() + '\n';
                    }
                });
                questionText = questionText.trim();

                const examples = $(question).find('.coderunner-examples tbody tr').map(function () {
                    return {
                        name: $(this).find('td:eq(0) pre').text().trim(),
                        input: $(this).find('td:eq(1) pre').text().trim(),
                        output: $(this).find('td:eq(2) pre').text().trim()
                    };
                }).get();

                const code = $(question).find('.ace_content').text().trim();

                const output = $(question).find('.coderunner-test-results tbody tr').map(function () {
                    return {
                        name: $(this).find('td:eq(1) pre').text().trim(),
                        input: $(this).find('td:eq(2) pre').text().trim(),
                        expected: $(this).find('td:eq(3) pre').text().trim(),
                        got: $(this).find('td:eq(4) pre').text().trim()
                    };
                }).get();

                return {
                    type: this.getType(),
                    text: questionText,
                    examples: examples,
                    code: code,
                    output: output
                };
            }
        };
    }

    const parsers = [getDescriptionParser(), getCoderunnerParser()];

    /**
     * Parses a single question element based on its type, using the appropriate parser.
     *
     * @param {Object} question The question DOM element.
     * @returns {Object | undefined}  The parsed question data as a JSON object, or undefined if no parser matches.
     */
    function parseQuestion(question) {
        for (let j = 0; j < parsers.length; j++) {
            const parser = parsers[j];
            if ($(question).hasClass(parser.getType())) {
                return parser.parseQuestionToJson(question);
            }
        }
    }

    /**
     * Parses all question elements on the page and returns an array of parsed question data.
     *
     * @returns {Array} An array of parsed question data objects.
     */
    function parse() {
        const questions = $(".que");
        const result = [];
        for (let i = 0; i < questions.length; i++) {
            const question = questions[i];
            result.push(parseQuestion(question));
        }
        return result;
    }

    return {
        init: function() {
            setTimeout(()=> {
                window.console.log(parse());
            }, 1000);
        },
        parse: parse
    };
});